
import Vue from 'vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'

import * as base from '@/api/base'
import { checkLoginToLogin } from '@/api/login'

Vue.use(ToastPlugin)
Vue.use(AjaxPlugin)

export default {
name: 'reflect',
components: {
XHeader,
XInput,
Group,
XButton,
Cell,
XDialog
},
data () {
return {
showScrollBox: false,
money: '',
phoneNumber: '',
authCode: '',
sendCodeText: '发送验证码',
refletMoneyText: '',
bankName: base.getSingleUserInfo('user_bankName'),
bankCode: base.getSingleUserInfo('user_bankCode'),
bankCardholder: base.getSingleUserInfo('user_bankCardholder')
}
},
methods: {
alert: function () {
if (!this.bankCode) {
this.$vux.toast.text('您未绑定银行卡', 'middle')
return false
}
this.showScrollBox = true
},
gotoChangeCard: function () {
this.$router.push({path: '/changeIdCard'})
},
getAuthCode (e) {
if (this.phoneNumber === '') {
this.$vux.toast.text('请填写手机号', 'middle')
return false
}
base.fetch('/api/v1/agentbanks/verificationCode', {phone: this.phoneNumber}, response => {
let data = response.data
let msg = data.msg || data.error || '请求失败'
this.$vux.toast.text(msg, 'middle')
if (data.code !== 0) return false
e.target.disabled = 'disabled'
let count = 60
this.sendCodeText = count + '秒'
let interval = setInterval(() => {
count--
this.sendCodeText = count + '秒'
if (count === 0) {
clearInterval(interval)
this.sendCodeText = '验证码'
e.target.removeAttribute('disabled')
}
}, 1000)
}, error => {
console.log(error)
})
},
submitAction () {
if (this.money === '') {
this.$vux.toast.text('请填写提现金额')
return false
}
if (this.phoneNumber === '') {
this.$vux.toast.text('请填写手机号码')
return false
}
if (this.authCode === '') {
this.$vux.toast.text('请填写验证码')
return false
}
let params = {
'money': this.money.toString(),
'phone': this.phoneNumber.toString(),
'authCode': this.authCode.toString()
}
base.fetch('/api/v1/agentbanks/withdrawals', params, response => {
let data = response.data
let msg = data.msg || data.error || '请求失败'
this.$vux.toast.text(msg, 'middle')
if (data.code !== 0) return false
let userMoney = base.getSingleUserInfo('user_money') - this.money
base.setUserInfo('user_money', userMoney)
this.$router.go(-1)
}, error => {
console.log(error)
})
}
},
created () {
checkLoginToLogin(this)
base.fetch('/api/v1/agents/userinfo', null, response => {
let data = response.data
this.refletMoneyText = '可提现' + data.money + '元'
}, error => {
console.log(error)
}, 'GET')
}
}



